import React from 'react';
import styles from './navgation.module.css'

const Navgation = ({menuToggle, menuCount}) => (
    <>
        <nav className={styles.nav}>
            <button className={styles.menuBtn} onClick={()=> menuToggle()} ><i className="fas fa-bars"></i></button>
            <ul className={ menuCount === 1 ? styles.active : styles.staic} >
                <li className={styles.menuLi}>Hong</li>
                <li className={styles.menuLi}>Jin</li>
            </ul>
        </nav>
    </>
    );

export default Navgation;