import './App.css';

import Main from './component/main/main';
import Festival from './data/festival'
import { useEffect, useState } from 'react';
import { HashRouter, Route} from 'react-router-dom';
import Detail from './component/detail';
import Header from './component/header/header';





function App() {
  const [ftv, setFtv] = useState([]);
  const [clickItem, setClickItem] = useState();
  const [detailInfo, setDetailInfo] = useState();
  const [detailImg, setDetailImg] = useState();
  const [mon, setMon] = useState(1);
  const [area, setArea] = useState('');
  const [season, setSeason] = useState(1);
  const [show, setShow] = useState(0);
  const [addShow, setAddShow] = useState(0);
  const [seasonList, setSeasonList] = useState([]);
  const [menuCount, setMenuCount] = useState(0);
  const year ='2021'
  
  const areaChange = (value) => setArea(value)

  const menuToggle = () => setMenuCount(menuCount === 0 ? menuCount + 1 : menuCount - 1)
  
  const seasonChange = (value, start, end) => {
    setSeason(value)
    setShow(0)

    festival   
    .seasonData(start, end)
    .then(seasonList => setSeasonList(seasonList));
  }
  
  const showDown = () => {
    setShow(show === 0 ? show + 1 : show - 1)
    show === 1 && window.scrollTo(0, 0)
  } 
  
  const addShowDown = () => setAddShow(addShow === 0 ? addShow + 1 : addShow - 1)
  const minus = (mon) => setMon(mon <= 1 ? mon = 12 : mon - 1)
  const plus = (mon) => setMon(mon >= 12 ? mon = 1 : mon + 1)
  
  
  const detail = (item) => {
        
        setClickItem(item)
        
        festival   
        .detailData(year, mon, area, item.contentid)
        .then(detailInfo => setDetailInfo(detailInfo));
        
        festival   
        .detailImg(item.contentid)
        .then(detailImg => setDetailImg(detailImg));
        
  } 
   const festival = new Festival() 

    useEffect(()=>{
    
    festival   
    .seasonData(20210301, 20210531)
    .then(seasonList => setSeasonList(seasonList));
  
    }, [])

    useEffect( () => {  
      
    festival   
    .festivalData(year, mon, area)
    .then(ftv => setFtv(ftv)); 
      
    }
    ,[area, mon])

     
     
  return (
      <HashRouter>
        <Header menuToggle={menuToggle} menuCount={menuCount} />
          <Route path="/" component={Main} exact={true}>
             <Main 
             ftv={ftv} 
             festival={festival}  
             onClickDetail={detail} 
             addShow={addShow} 
             addShowDown={addShowDown} 
             show={show} 
             showDown={showDown} 
             mon={mon} 
             area={area} 
             season={season} 
             seasonChange={seasonChange} 
             areaChange={areaChange} 
             minus={minus} 
             plus={plus} 
             seasonDateList={seasonList}
             />
          </Route>
          
          <Route path="/detail" component={Detail}>
            <Detail  clickItem={clickItem} detailInfo={detailInfo} detailImg={detailImg} />
          </Route>
       
      </HashRouter>
  );
}

export default App;
